import Vue from "@/main";
import axios from "axios";
import store from "../store";
import {
  Message
} from "element-ui";
import qs from 'qs'
// import commonConfig from '../config.js';
var loadingTime = 500;
axios.defaults.timeout = 15000;
axios.interceptors.request.use(
  config => {
    // console.log('!',config)
    if (store.state.token) {
      // config.headers.token = store.state.token;           //
      config.headers['X-CSRF-TOKEN'] = store.state.token;

    }
    // config.transformRequest = [function (data) {
    //   // data
    //   console.log(data)
    //   data = qs.stringify(data)
    //   console.log(data)
    //   return data
    // }]
    return config;
  },
  function (error) {
    // console.log('!',error)
    return Promise.reject(error);
  }
);
//
axios.interceptors.response.use(
  res => {
    //  200
    // debugger
    // console.log(123)
    Vue.$httpLoading.close();
    //  => 
    if (res.status == 200 && res.data.code == '0') {
      return Promise.resolve(res);
    } else if (res.status == 200 && res.data.code == '-1') {
      return Promise.resolve(res);
    } else if (res.status == 200 && res.data.code == '1') {
      return Promise.resolve(res);
    }
    else if (res.status == 200 && res.data.type) {
      //   => 
      return Promise.resolve(res);
    } else {
      //   => 
      return Promise.reject(res);
    }
  },
  error => {
    Vue.$httpLoading.close();
    // console.log("!", error);
    // console.log(error.response);
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.msg = "(400)";
          break;
        case 401:
          error.msg = "(401)";
          break;
        case 403:
          error.msg = "(403)";
          break;
        case 404:
          error.msg = "(404)";
          break;
        case 408:
          error.msg = "(408)";
          break;
        case 500:
          error.msg = "(500)";
          break;
        case 501:
          error.msg = "(501)";
          break;
        case 502:
          error.msg = "(502)";
          break;
        case 503:
          error.msg = "(503)";
          break;
        case 504:
          error.msg = "(504)";
          break;
        case 505:
          error.msg = "HTTP(505)";
          break;
        default:
          error.msg = `(${error.response.status})!`;
      }
    } else {
      error.msg = "!";
    }
    Message.error(error.msg);
    return;
    // return Promise.reject(error)
  }
);

export function requestGet (XMLObject) {
  // config => ; url=>  , params=>  isLoading => loading , loadingTime => loading
  return new Promise((resolve, reject) => {
    let showLoading =
      XMLObject.showLoading || XMLObject.showLoading === undefined ?
        true :
        XMLObject.showLoading;
    if (showLoading) {
      let time = XMLObject.loadingTime || loadingTime;
      console.dir(Vue.$httpLoading);
      Vue.$httpLoading.show();
      setTimeout(() => {
        resolve(XMLObject);
      }, time);
    } else {
      Vue.$httpLoading.close();
      resolve(XMLObject);
    }
  }).then(res => {
    return axios.get(res.url, {
      params: res.data
    });
  });
}
export function requestPost (XMLObject) {
  // debugger
  let showLoading =
    XMLObject.showLoading || XMLObject.showLoading === undefined ?
      true :
      XMLObject.showLoading;
  let formData = XMLObject.data
  return new Promise((resolve, reject) => {
    if (showLoading) {
      let time = XMLObject.loadingTime || loadingTime;
      Vue.$httpLoading.show();
      setTimeout(() => {
        resolve(XMLObject);
      }, time);
    } else {
      Vue.$httpLoading.close();
      resolve(XMLObject);
    }
  }).then(res => {
    return axios.post(res.url, formData);
  });
}
export function requestFile (XMLObject) {
  // debugger
  let showLoading =
    XMLObject.showLoading || XMLObject.showLoading === undefined ?
      true :
      XMLObject.showLoading;
  let formData = new FormData();
  Object.keys(XMLObject.data).forEach(key => {
    formData.append(key, XMLObject.data[key]);
  });
  return new Promise((resolve, reject) => {
    if (showLoading) {
      let time = XMLObject.loadingTime || loadingTime;
      Vue.$httpLoading.show();
      setTimeout(() => {
        resolve(XMLObject);
      }, time);
    } else {
      Vue.$httpLoading.close();
      resolve(XMLObject);
    }
  }).then(res => {
    return axios.post(res.url, formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });
  });
}


